import { createStore } from "vuex";
import characterMock from "@/mocks/characterMock";

export default createStore({
  state: {
    character: characterMock,
    isInitialDataLoaded: false,
    authToken: "",
    isAuthorized: false,
  },
  mutations: {
    setAuthToken: (state, data) => {
      state.authToken = data;
    },
  },
  actions: {},
  modules: {},
});
